<template>
  <div class="mapWrp">
    <div id="maps">
      <ul class="dateWrp">
        <li
          v-for="(item,index) in weeks"
          :key="index"
          @click="showLen(index)"
        >{{item.datestr.substring(5)}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import localIcon from "@/assets/local1.png";
import bluelocalIcon from "@/assets/local2.png";
export default {
  data() {
    return {
      mass: null,
      map: null,
      currentMarker: null,
      weeks: null,
      currentPath:[{lnglat: [120.675555, 28.000575]},{lnglat: [120.773555, 28.030575]},{lnglat: [120.645555, 28.050575]}]
    };
  },
  mounted() {
    this.builtMap(120.675555, 28.000575);
    // this.setMarker(120.675555, 28.000575);
    this.weekDay();
    this.massfuc()
  },
  methods: {
    async coverAdrs(lnglat) {
      var geocoder = new AMap.Geocoder();
      return new Promise(function(resolve, reject) {
        geocoder.getAddress(lnglat, function(status, result) {
          if (status === "complete" && result.info === "OK") {
            resolve(result.regeocode.formattedAddress);
          } else {
            reject("逆地址结果请求失败");
          }
        });
      });
    },
    async massfuc() {
      let that = this;
      if (!this.currentPath) {
        return false;
      }
      let mass = new AMap.MassMarks(this.currentPath, {
        opacity: 0.86,
        zIndex: 111,
        cursor: "pointer",
        style: {
          url: localIcon,
          anchor: new AMap.Pixel(23, 23),
          size: new AMap.Size(25, 25)
        }
      });
      this.mass = mass;
      mass.on("click", async function(e) {
        let coverAdrs = await that.coverAdrs(
          that.currentPath[e.data.index].lnglat
        );
        console.log(coverAdrs);
        let infoWin = that.makeInfoWindow(
          coverAdrs,
          new Date(
            that.currentPath[e.data.index].pointTime * 1000
          ).toLocaleDateString() +
            new Date(
              that.currentPath[e.data.index].pointTime * 1000
            ).toLocaleTimeString()
        );
        infoWin.open(that.map, that.currentPath[e.data.index].lnglat);
      });
      mass.setMap(this.map);
      this.map.setCenter(this.currentPath[0].lnglat);
      this.map.setZoom(12);
    },
    showLen(indx) {
      console.log(indx);
    },
    makePointInfoWindow(infos) {
      let info = [];
      info.push("<div class='input-card content-window-card pointInfos'>");
      info.push(
        "<p class='input-item'><span>电量：" +
          infos.power +
          "</span><span>" +
          " 步数：" +
          infos.stepNum +
          "</span></p>"
      );
      info.push(
        "<p class='input-item'>点位时间：" +
          new Date(infos.locationTime * 1000).toLocaleDateString() +
          new Date(infos.locationTime * 1000).toLocaleTimeString() +
          "</p></div></div>"
      );
      let infoWindow = new AMap.InfoWindow({
        content: info.join(""),
        offset: new AMap.Pixel(0, -57)
      });
      this.infoWindow = infoWindow;
      return infoWindow;
    },
    setMarker(lng, lat) {
      let icon = new AMap.Icon({
        size: new AMap.Size(23, 23),
        image: bluelocalIcon,
        imageSize: new AMap.Size(23, 23)
      });

      let marker = new AMap.Marker({
        position: new AMap.LngLat(lng, lat),
        animation: "AMAP_ANIMATION_BOUNCE",
        icon: icon
      });
      this.currentMarker = marker;
      this.map.add(marker);
    },
    builtMap(lng, lat) {
      var map = new AMap.Map("maps", {
        zoom: 11,
        center: [lng, lat],
        viewMode: "2D",
        lang: "zh_cn"
      });
      this.map = map;
    },
    weekDay() {
      let weeks = [];
      let year = 0;
      let month = 0;
      let date = 0;
      year = new Date().getFullYear();
      month = new Date().getMonth();
      date = new Date().getDate(month);
      let dateNow = Date.parse(new Date(year, month, date));
      console.log(dateNow);
      for (let i = 0; i < 7; i++) {
        let tempDate = dateNow - 24 * 60 * 60 * 1000 * i;
        let tempStr =
          new Date(tempDate).getFullYear() +
          "-" +
          this.toDuble(new Date(tempDate).getMonth() + 1) +
          "-" +
          this.toDuble(new Date(tempDate).getDate());
        let tempDateObj = {
          Datems: tempDate / 1000,
          datestr: tempStr
        };
        weeks.push(tempDateObj);
      }
      this.weeks = weeks;
    },
    toDuble(num) {
      return String(num).length == 1 ? "0" + num : num;
    }
  }
};
</script>

<style scoped>
.mapWrp {
  position: relative;
  width: 100%;
  height: 100%;
}
#maps {
  width: 100%;
  height: 100%;
}
.dateWrp {
  list-style: none;
  border-radius: 6px;
  box-shadow: 0 0 12px #ddd;
  background-color: #fff;
  z-index: 3;
  line-height: 30px;
  padding: 0;
  position: absolute;
  top: 3%;
  right: 3%;
}
.dateWrp li {
  padding: 6px 12px;
  cursor: pointer;
  width: 120px;
  box-sizing: border-box;
  margin-bottom: 6px;
  border-bottom: 1px solid #ccc;
}
.dateWrp li:last-child {
  border: none;
}
</style>